/*viewport********************************************************************/

@media only screen and (min-width: 1440px) {
  article {
    transform-origin: top;
    transform: rotateX(90deg);
    max-height: 0px;
    animation: displaynone 700ms ease-in-out forwards 1400ms;
    &:not(.about) section:not(.animatedBackground) {
      opacity: 0;
      animation: fadein 700ms ease-in-out forwards 2100ms;
    }
  }

  footer {
    transform-origin: top;
    max-height: 0px;
    transform: rotateX(90deg);
    opacity: 0;
    animation: displaynone 700ms ease-in forwards 1400ms;
    * {
      opacity: 0;
      animation: fadein 700ms ease-in-out forwards 2100ms;
    }
  }
}

/*font************************************************************************/
@import url("https://fonts.googleapis.com/css?family=Noto%20Sans:400,700");

h1 {
  font-family: Origam;
  font-size: 3em;
  letter-spacing: 0.2em;
  line-height: 1.5em;
  padding-left: 0.2em;
  color: #000000;
}

h2 {
  font-family: "Noto Sans";
  font-weight: 500;
  font-size: 4em;
  letter-spacing: 0.6em;
  padding-left: 0.6em;
  color: white;
  background: url(https://erebos.xyz/PIC/kreide.png);
  background-size: 21px;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

h3 {
  font-family: "Noto Sans";
  font-weight: 500;
  font-size: 1.8em;
  letter-spacing: 0.6em;
  padding-left: 0.6em;
  color: white;
  background: url(https://erebos.xyz/PIC/kreide.png);
  background-size: 21px;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

h4 {
  font-size: 1.2em;
  font-family: "Noto Sans";
  color: black;
}

h5 {
  display: block;
  font-size: 1em;
  margin-bottom: 0.33em;
  margin-left: 0;
  margin-right: 0;
  font-weight: bold;
}

a {
  text-decoration: none;
  color: #000000;
}

blink {
  animation-duration: 1s;
  animation-name: blink;
  animation-iteration-count: infinite;
  animation-timing-function: steps(2, start);
}

