@import "../../../utilities/variables";

/*viewport***************************************************************************************************************/

@media only screen and (max-width: 1440px) {
  .admins-s {
    flex-direction: column;
    .admins-h {
      display: flex;
      align-items: center;
      border-bottom: solid #a80000 1px;
      width: 100vw;
      height: 100px;
    }
    .members {
      flex-direction: row;
      .mrow {
        flex-direction: column;
        .member {
          .profile {
            flex-direction: row;
            .pic {
              display: none;
              position: absolute;
            }
            .hollow {
              box-shadow: 0 0 0 13px rgba(0, 0, 0, 0.2),
                inset 0 0 3px rgba(115, 114, 23, 0.8);
              background-attachment: fixed;
              background-image: url(https://erebos.xyz/PIC/m33.png);
              .iback {
                position: absolute;
                background-color: #000;
                transform: rotate3d(0, 1, 0, 180deg);
                color: #fff;
                font-size: 25px;
              }
              .ifront {
                visibility: visible;
                position: relative;
              }
            }
            &:hover .hollow {
              box-shadow: 0 0 0 0px rgba(255, 255, 255, 0.3),
                inset 0 0 3px rgba(115, 114, 23, 0.8);
            }
            &:hover .hollow .info {
              transform-style: preserve-3d;
              transform: rotate3d(0, 1, 0, -180deg);
            }
          }
        }
      }
    }
  }
  .mods-s {
    flex-direction: column;
    .mods-h {
      display: flex;
      align-items: center;
      border-bottom: solid #2a2a2a 1px;
      width: 100vw;
      height: 100px;
    }
    .members {
      flex-direction: row;
      .mrow {
        flex-direction: column;
