              }
              .ifront {
                visibility: visible;
                position: relative;
              }
            }
            &:hover .hollow {
              box-shadow: 0 0 0 0px rgba(255, 255, 255, 0.3),
                inset 0 0 3px rgba(115, 114, 23, 0.8);
            }
            &:hover .hollow .info {
              transform-style: preserve-3d;
              transform: rotate3d(0, 1, 0, -180deg);
