/*section*sharingan-s****************************************************************************************************/

.community {
}

.community-s {
  display: flex;
  align-items: center;
  top: $navtop;
  min-height: 100vh;
}

.admins-h {
  display: flex;
  justify-content: center;
  align-items: center;
}

.admins-s {
  background-color: #2a2a2a;
  .members {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: calc(100vh - 320px);
    width: 100%;
    .mrow {
      display: flex;
      justify-content: space-around;
      align-items: center;
      width: 100%;
      margin: 30px;
      .member {
        flex: 0.1;
        .profile {
          top: 0px;
          display: flex;
          justify-content: center;
          align-items: center;
          position: relative;
          min-width: 100px;
          .pic {
            width: 100%;
            img {
              top: 0px;
              border-radius: 50%;
              width: 100%;
              height: auto;
              // don't think about the line below
              margin: 0px 0px -5px 0px;
            }
          }
          .hollow {
            position: relative;
            border-radius: 50%;
            margin: 11%;
            padding: 1%;
            transition: all 0.4s ease-in-out;
            .info {
              transition: all 0.4s ease-in-out;
              transform-style: preserve-3d;
              margin: -2.5%;
              .iback {
                display: flex;
                flex-direction: column;
                top: 0px;
                right: 0px;
                align-items: center;
                justify-content: center;
                width: 100%;
                height: 100%;
                border-radius: 50%;
                -webkit-backface-visibility: hidden;
                backface-visibility: hidden;
                h5 {
                  display: inline;
                }
                p {
                  font-size: 0.5em;
                }
              }
              .ifront {
                overflow: hidden;
                border-radius: 50%;
                img {
                  position: relative;
                  width: 100%;
                  transform: scale(1.25);
                  height: auto;
                  // don't think about the line below
                  margin: 0px 0px -5px 0px;
                  -webkit-backface-visibility: hidden;
                  backface-visibility: hidden;
                }
              }
            }
          }
        }
      }
    }
  }
}

.mods-h {
  display: flex;
  justify-content: center;
  align-items: center;
}

.mods-s {
  background-color: #a80000;
